%h1 {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
}

%font-poppins {
  font-family: Poppins, sans-serif;
  font-size: 13px;
  font-weight: 700;
  line-height: 0px;
  letter-spacing: 3px;
  text-transform: uppercase;

  text-decoration: none;
  color: #fff;

  padding: 20px 0;

  border-bottom: 1px solid transparent;
}
